const startTimer = (winningConfig) => {
  const $currentScore = document.querySelector("#currentScore");
  document.querySelector(".timer").classList.add("active");
  //let timeLimit = winningConfig.timeLimit;
  let counter = winningConfig.timeLimit;

  let gameTimer = setInterval(function () {
    if (counter <= 0) {
      clearInterval(gameTimer);
      document.querySelector("#clearBtn").click();
    }
    document.getElementById("stopWatch").innerHTML =
      "00:" + zeroPad(counter, 2);
    counter = counter - 1;
    if (counter >= 0 && +$currentScore.innerHTML >= 10) {
      openModal();
      //alert('you have won!');
      clearInterval(gameTimer);
      document.querySelector("#clearBtn").click();
      document.getElementById("stopWatch").innerHTML = "00:00";
      //alert('you loose');
    }
  }, 1000);

  function zeroPad(num, places) {
    var zero = places - num.toString().length + 1;
    return Array(+(zero > 0 && zero)).join("0") + num;
  }
};

const clearTimer = (timerId) => {
  clearInterval(timerId);
};

export { startTimer, clearTimer };
